"use strict";

var React = require("react");
var Snippet = require("../Snippet.react");
var request = require("superagent");

module.exports = React.createClass({
    displayName: "exports",


    getInitialState: function () {
        return {
            post: this.props.post || {},
            project: this.props.project || {}
        };
    },

    componentDidMount: function () {
        var self = this;

        if (!Object.keys(this.state.project).length) {
            request.get("/").query({
                isClient: true
            }).end(function (res) {
                var data = JSON.parse(res.text);
                self.setState({
                    project: data.project,
                    post: data.post
                });
            });
        }
    },

    render: function () {
        return React.createElement(
            "section",
            { className: "home skinny" },
            React.createElement(
                "div",
                { className: "grid grid--centered" },
                React.createElement(
                    "div",
                    { className: "avatar grid--1-2 grid--left" },
                    React.createElement("img", { className: "avatar__image", src: "/static/images/me_alt.jpeg" }),
                    React.createElement(
                        "div",
                        { className: "avatar__summary" },
                        React.createElement(
                            "p",
                            { className: "avatar__summary__item" },
                            "Nick Ball"
                        ),
                        React.createElement(
                            "p",
                            { className: "avatar__summary__position avatar__summary__item" },
                            "Jr. Software Engineer"
                        ),
                        React.createElement(
                            "p",
                            { className: "avatar__summary__employer avatar__summary__item" },
                            "@ Loudr"
                        )
                    )
                ),
                React.createElement(
                    "div",
                    { className: "tagline grid--1-2 grid--last" },
                    React.createElement(
                        "p",
                        { className: "tagline__item" },
                        "Development +"
                    ),
                    React.createElement(
                        "p",
                        { className: "tagline__item" },
                        "Design +"
                    ),
                    React.createElement(
                        "p",
                        { className: "tagline__item" },
                        "Etc."
                    )
                )
            ),
            React.createElement("hr", { className: "rule rule--small" })
        );
    }

});
