"use strict";

var React = require("react");
var navigate = require("react-mini-router").navigate;
var request = require("superagent");
var ErrorList = require("../shared/ErrorList");

module.exports = React.createClass({
    displayName: "exports",


    getInitialState: function () {
        return {
            hasErrors: false,
            errors: []
        };
    },

    componentDidMount: function () {},

    render: function () {
        return React.createElement(
            "section",
            { className: "login skinny" },
            React.createElement(
                "h1",
                null,
                "Login"
            ),
            React.createElement(
                "form",
                { action: "/login", method: "post", onSubmit: this.handleSubmit },
                React.createElement(
                    "div",
                    { className: "form-row" },
                    React.createElement(
                        "label",
                        { htmlFor: "username" },
                        "Username"
                    ),
                    React.createElement("input", {
                        type: "text",
                        name: "username",
                        ref: "username"
                    })
                ),
                React.createElement(
                    "div",
                    { className: "form-row" },
                    React.createElement(
                        "label",
                        { htmlFor: "password" },
                        "Password"
                    ),
                    React.createElement("input", {
                        type: "password",
                        name: "password",
                        ref: "password"
                    })
                ),
                React.createElement(
                    "div",
                    { className: "form-row" },
                    React.createElement(
                        "button",
                        { className: "button", type: "submit" },
                        "Submit"
                    )
                )
            ),
            React.createElement(ErrorList, { errors: this.state.errors })
        );
    },

    handleSubmit: function (e) {
        var self = this;
        e.preventDefault();
        var username = this.refs.username.getDOMNode().value.trim();
        var password = this.refs.password.getDOMNode().value.trim();

        request.post("/login").query({
            isClient: true
        }).send({
            username: username,
            password: password
        }).end(function (res) {
            var resp = JSON.parse(res.text);
            if (resp.success) {
                navigate("/admin");
            } else {
                self.setState({
                    errors: resp.errors
                });
            }
        });
    }


});
